@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&family=Plus+Jakarta+Sans:ital,wght@0,300;0,400;0,500;1,600&family=Poppins:wght@300;400;500&display=swap');

* {
  margin:0;
  padding: 0;
  box-sizing: border-box;
}

/*
font-family: 'Poppins', sans-serif;
*/

/*navbar*/
.navbar {
  font-family: 'Poppins', sans-serif !important;
  font-size: 17px !important;
  line-height: 26px;
  /* text-transform: uppercase; */
  box-shadow: 0 4px 6px -6px gray;
  --webkit-box-shadow: 0 4px 6px -6px gray;
}

.nav-link {
  font-weight: 500 !important;
}

.navbar-brand {
  font-weight: bolder !important;
  letter-spacing: 1px;
}

.active {
  border-bottom: 1px solid black;
}

.footer {
  color: white;
  padding: 20px;
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 12px;
  bottom: 0;
}
.footer a:hover {
  color: rgb(145, 140, 140);
}

.contactus {
  margin: 0;
  padding: 0;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.policy {
  margin: 0;
  padding: 0;
  height: 100vh;
  display: flex;
  justify-content: center;
}

.about {
  margin: 0;
  padding: 0;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.contactus h1 {
  border-radius: 10px;
}
.policy h1 {
  border-radius: 10px;
}

.register {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  flex-wrap: wrap;
  flex-direction: column;
}

.register form{
  display: flex;
  flex-direction: column;
  width: 400px;
}
.login {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  flex-wrap: wrap;
  flex-direction: column;
}

.login form{
  display: flex;
  flex-direction: column;
  width: 400px;
}

/* .register form input {
  border: none !important;
  border-radius: 0%;
  border-bottom: 1px solid gray !important;
} */

@media only screen and (max-width: 400px) {
  .about {
    margin-bottom: 50px;
  }
  .register form{
    width: 90vw;
  }
}

.product-link {
  text-decoration: none !important;
  color: black !important;
}

.badge-dark{
  background-color: gray !important;
  color: white !important;
  border-radius: 4px !important;
}